.profile {
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 2.8891rem;
  min-width: 55rem;
}

@media screen and (min-width: 20rem) and (max-width: 47.9375rem) {
  .profile {
    flex-direction: column;
    min-width: auto;
    min-width: auto;
  }
}

@media screen and (min-width:48rem) and (max-width: 63.9375rem) {
  .profile {
    font-size: 2.4rem;
    min-width: 500px;
  }
}