.modal {
  background-color: white;
  display: flex;
  flex-direction: column;
  padding: 2.25rem;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  max-width: 358px;
}

@media screen and (min-width: 20rem) and (max-width: 767px) {
  .modal {
    padding: 25px 22px;
  }
}